<template>
  <v-app>
    <v-main>
      <v-card width="600" style="margin: 120px auto 0 auto" elevation="1">
        <v-container class="header-section py-7">
          <v-row>
            <v-col cols="auto" class="mr-auto" align-self="center">
              <v-col class="py-0 text-h6 font-weight-bold">SHOP NAME</v-col>
              <v-col class="py-0 price-tag font-weight-bold">¥ 99,999</v-col>
            </v-col>
            <v-col cols="auto" align-self="center" class="pr-8">
              <v-avatar class="pa-11 elevation-2 white">
                <img
                  id="logo"
                  src="https://images.ctfassets.net/i7it8bepy72j/jLJhzOAtmq7IyzAhg8RkD/70fe2861e845382adf77457adab78f11/logo-512.png"
                  alt="logo"
                />
              </v-avatar>
            </v-col>
          </v-row>
        </v-container>
        <v-container>
          <v-form id="form">
            <v-container class="mb-15">
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    label="メールアドレス"
                    type="text"
                    id="email"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    label="携帯電話番号"
                    type="text"
                    id="telephone"
                    @keydown="validateTelephoneNumber"
                    maxlength="13"
                    :rules="[validateTelephoneFormat]"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-container>
            <v-container class="text-center">
              <v-col class="text-caption pb-0"
                ><a id="terms" class="text-decoration-none" href="https://terms.paidy.com/"
                  ><span>利用規約・個人情報取扱条項</span></a
                >に同意して</v-col
              >
              <v-col>
                <v-btn id="submit" x-large class="submit-button" rounded
                  >次へ</v-btn
                >
              </v-col>
            </v-container>
          </v-form>
        </v-container>
      </v-card>
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: 'App',

  data: () => ({
    //
  }),

  methods: {
    validateTelephoneNumber: (event) => {
      if ((event.key && event.key.length > 1) || /^\d|-$/.test(event.key)) {
        return true;
      } else {
        event.preventDefault();
        return false;
      }
    },
    validateTelephoneFormat: (value) => {
      return value &&
        value !== null &&
        value !== undefined &&
        /^d{1,4}-\d{4}$|^\d{2,5}-\d{1,4}-\d{4}$/.test(value)
        ? true
        : '080-1001-1001または08-1001-1001の形式で入力してください';
    },
  },
};
</script>

